* {
  box-sizing: border-box;
  color: #fff;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url("../img/bg.png");
  line-height: 1.5;
}

#search-box, #filter {
  font: inherit;
  color: #000;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info__image {
  width:200px;
}

.country-info__label {
  font-size: 22px;
}

.country-list__image {
  width: 50px;
}

.country-list {
  display: flex;
  flex-flow: column;
  list-style: none;
}

.country-list__name {
  margin-left: 10px;
}

.country-list__item {
  display: flex;
  align-items: center;
  width: 250px;
  height: 50px;
  padding: 5px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.33);
  
}

.country-list__item:last-of-type {
  border-bottom: none;
}

